        <div class="modal-body text-start">

          <b>Doble condn doble proteccin</b>: Falso. La doble proteccin hace referencia es a protegerse de dos
          situaciones:
          un embarazo no planeado y las ITS. Es completamente falso que usar doble condn sirva para protegerse mejor de
          las ITS. Todo lo contrario, existe mayor riesgo de que se rompan o se deslicen cuando se usan al tiempo. Es
          suficiente con usar un solo condn de manera. <br>
          <b> Si tiene una ITS, eso se notar</b>: Falso. Este es uno de los mitos ms peligrosos para la salud. Algunas
          ITS no
          se manifiestan (como el herpes genital) o, a veces, los sntomas pueden tardar un buen tiempo en aparecer. Por
          eso, la mejor opcin es hacerse exmenes de laboratorio preventivos de manera peridica. El diagnstico temprano
          es clave para controlar las ITS, especialmente en el caso del VIH. <br>

          <b> Solo las personas que tienen varias parejas sexuales tienen el riesgo</b>: Falso. Es cierto que cuantas
          ms
          parejas sexuales se tengan, ms expuesto se est a infectarse. Pero eso no quiere decir que las parejas estables
          estn libres de las ITS pues es posible que la infeccin llegue en un momento de nuestra vida, sin necesidad de
          desarrollar sntomas y que, ms adelante, contagiemos a nuestra pareja actual. <br>

          <b> La pldora anticonceptiva protege de las ITS</b>: Falso. Las infecciones de transmisin sexual se contraen
          por
          contacto directo con la sangre infectada, por contacto con los fluidos corporales, por el uso compartido de
          equipos de inyeccin, de la madre al hijo, incluso, mediante el contacto piel con piel. Por tanto, la pldora
          anticonceptiva, un compuesto de hormonas que son eficaces para evitar un embarazo, no protege contra las
          infecciones de transmisin. <br>

          <b> Es imposible tratar las infecciones de trasmisin sexual</b>: Falso. Para todas las ITS existe
          tratamiento. Lo que
          sucede es que no todas son curables, como el VIH. Si llegas a tener algn cambio en el cuerpo o alguna relacin
          sexual de riesgo, lo recomendable es consultar al servicio de salud y hablar de la situacin sin temor.

